@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-400.woff2") format("woff2"), url("../fonts/pt-mono-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-500.woff2") format("woff2"), url("../fonts/roboto-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2"), url("../fonts/roboto-condensed-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PT Mono", "Lucida Console", monospace;
  color: #041827;
  word-break: break-word;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #041827;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 50px;
  box-sizing: content-box;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  display: inline-block;
  vertical-align: auto;
  padding: 10px 15px;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  color: #f2f5f6;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: none;
  border: 1px solid;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.button--red {
  background-color: #ff1553;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
  .button:hover:not(.focus-visible):active, .button:hover:not(.focus-visible).focus-visible:active {
    opacity: 0.3;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}
.button.focus-visible:focus:active, .button.focus-visible:focus.focus-visible:active {
  opacity: 0.3;
}
.button:disabled {
  color: #606060;
  border-color: #f2f2f2;
  background-color: #f2f2f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7019607843);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}

.modal__wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  min-height: 610px;
  margin: 85px auto auto;
  padding: 40px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 40px 20px 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    opacity: 0.8;
    outline: 1px solid #f8fdff;
    outline-offset: 2px;
  }
}
.modal__close-btn.focus-visible:focus {
  opacity: 0.8;
  outline: 1px solid #f8fdff;
  outline-offset: 2px;
}
.modal__close-btn:active svg, .modal__close-btn.focus-visible:active svg {
  fill: #041827;
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
  margin-bottom: 5px;
  color: #ffffff;
}

.modal p {
  margin: 0;
  margin-bottom: 38px;
  color: #ffffff;
}

.modal__form fieldset {
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: none;
  padding: 0;
  margin-bottom: 15px;
}

.modal__form input:not(.visually-hidden),
.modal__form textarea {
  background-color: rgba(4, 20, 39, 0.1490196078);
  border: none;
  border-radius: 3px;
  color: #041827;
  padding: 15px;
  width: 100%;
}
@media (hover: hover) {
  .modal__form input:not(.visually-hidden):hover:not(.focus-visible),
.modal__form textarea:hover:not(.focus-visible) {
    outline: 1px solid #f8fdff;
    outline-offset: 2px;
    cursor: pointer;
  }
  .modal__form input:not(.visually-hidden):hover:not(.focus-visible):disabled,
.modal__form textarea:hover:not(.focus-visible):disabled {
    outline: none;
  }
}
.modal__form input:not(.visually-hidden).focus-visible:focus,
.modal__form textarea.focus-visible:focus {
  outline: 1px solid #f8fdff;
  outline-offset: 2px;
  cursor: pointer;
}
.modal__form input:not(.visually-hidden).focus-visible:focus:disabled,
.modal__form textarea.focus-visible:focus:disabled {
  outline: none;
}
.modal__form input:not(.visually-hidden):disabled,
.modal__form textarea:disabled {
  pointer-events: none;
  border-color: #bfbfbf;
  background-color: #bfbfbf;
}
.modal__form input:not(.visually-hidden):disabled::placeholder,
.modal__form textarea:disabled::placeholder {
  color: #606060;
}

.modal__form-field textarea {
  padding: 25px 15px;
}

.modal__form-field input::placeholder,
.modal__form-field textarea::placeholder {
  color: #ffffff;
}

.modal__form button {
  font-size: 16px;
  line-height: 19px;
  background-color: #041427;
  color: #ffffff;
  border: none;
  padding: 15px 25px;
  width: 100%;
  margin-bottom: 14px;
}

.modal__form-label {
  display: inline-block;
  vertical-align: auto;
  position: relative;
  cursor: pointer;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  opacity: 0.8;
  padding-left: 30px;
  margin-left: 5px;
}
.modal__form-label::before {
  content: "";
  position: absolute;
  left: 5px;
  top: 50%;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  transform: translateY(-50%);
}
@media (hover: hover) {
  .modal__form-label:hover:not(.focus-visible) {
    color: #041427;
  }
}

.modal__form-container-inner {
  position: relative;
}

.modal__form-container-inner input {
  left: 19px;
  bottom: 15%;
}

.modal__form-container-inner input:focus + .modal__form-label::before,
.modal__form-container-inner input:focus-visible + .modal__form-label::before {
  outline: 1px solid #f8fdff;
  outline-offset: 2px;
}

.modal__form-container-inner input:checked + .modal__form-label::after {
  content: "";
  position: absolute;
  width: 9px;
  height: 6px;
  background-image: url("../img/svg/check-mark.svg");
  background-size: 9px 6px;
  background-repeat: no-repeat;
  top: 50%;
  left: 5px;
  transform: translate(35%, -50%);
}

.main-header {
  position: fixed;
  z-index: 10;
  top: 0;
  width: 100vw;
  padding-top: 30px;
  padding-bottom: 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .main-header {
    position: relative;
    top: auto;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.main-header__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-header__container {
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .main-header__container {
    align-items: center;
  }
}

.main-header__contacts {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 28px;
  max-width: 480px;
  margin-left: auto;
  margin-right: -18px;
  margin-top: -8px;
}
@media (max-width: 1023px) {
  .main-header__contacts {
    gap: 20px;
    margin-top: unset;
    margin-right: 20px;
    max-width: 440px;
    justify-content: flex-end;
  }
}
@media (max-width: 767px) {
  .main-header__contacts {
    max-width: 200px;
    margin-right: 10px;
  }
}

.main-header__contacts-link {
  display: flex;
  align-items: center;
  min-width: 225px;
  gap: 15px;
}
@media (max-width: 1023px) {
  .main-header__contacts-link {
    justify-content: center;
    min-width: auto;
    width: 40px;
    height: 40px;
  }
}
@media (hover: hover) {
  .main-header__contacts-link:hover:not(.focus-visible) svg {
    fill: #f2f5f7;
  }
}
.main-header__contacts-link.focus-visible:focus svg {
  fill: #f2f5f7;
}

.main-header__contacts-link span {
  display: inline-block;
  line-height: 18px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .main-header__contacts-link span {
    display: none;
  }
}

.main-header__container button {
  font-size: 14px;
  line-height: 16px;
  max-width: 200px;
  margin-right: 5%;
  margin-left: auto;
  margin-top: -7px;
}
@media (max-width: 1023px) {
  .main-header__container button {
    margin-top: unset;
    display: none;
  }
}

.main-logo {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto auto;
  column-gap: 15px;
  cursor: default;
  max-width: 205px;
  margin-top: 0;
}
@media (max-width: 767px) {
  .main-logo {
    width: 35px;
    height: 35px;
    display: inline-block;
    vertical-align: auto;
    margin-top: 4px;
  }
}
@media (hover: hover) {
  .main-logo:hover:not(.focus-visible) svg {
    fill: #f2f5f7;
  }
}
.main-logo.focus-visible:focus svg {
  fill: #f2f5f7;
}

.main-logo svg {
  grid-row: 1/-1;
}
@media (max-width: 767px) {
  .main-logo svg {
    width: 35px;
    height: 35px;
  }
}

.main-logo__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 29px;
  color: #f2f5f7;
  margin-top: 2px;
}

.main-logo__text {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7019607843);
}

@media (max-width: 767px) {
  .main-logo__title,
.main-logo__text {
    display: none;
  }
}

.main-footer {
  padding-top: 52px;
  padding-bottom: 55px;
  background-color: #041827;
}
@media (max-width: 767px) {
  .main-footer {
    padding-top: 35px;
    padding-bottom: 35px;
  }
}

.main-footer__container {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-footer__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 32px;
    column-gap: 4%;
  }
