    height: 40px;
    background-size: 4px 8px;
  }
}

.products__image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  background-color: #041827;
}

@media (max-width: 1023px) {
  .products__image--desktop {
    display: none;
  }
}
@media (max-width: 767px) {
  .products__image--desktop {
    display: none;
  }
}

.products__image--tablet {
  display: none;
}
@media (max-width: 1023px) {
  .products__image--tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .products__image--tablet {
    display: none;
  }
}

.products__image--mobile {
  display: none;
}
@media (max-width: 1023px) {
  .products__image--mobile {
    display: none;
  }
}
@media (max-width: 767px) {
  .products__image--mobile {
    display: block;
  }
}

.products__link p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
