.contact {
  padding-top: 165px;
  padding-bottom: 250px;
  background-color: #00b2ff;
  position: relative;
  overflow: hidden;
}
.contact::before {
  content: "";
  position: absolute;
  background-color: rgba(4, 24, 39, 0.5019607843);
  width: 100vw;
  height: 100vw;
  top: -56vw;
  left: -69vw;
  transform: rotate(-40.6deg);
}
@media (max-width: 1023px) {
  .contact {
    padding-top: 46px;
    padding-bottom: 125px;
  }
  .contact::before {
    display: none;
  }
}
@media (max-width: 767px) {
  .contact {
    padding-top: 48px;
    padding-bottom: 85px;
  }
}

.contact__container {
  position: relative;
}
.contact__container::before {
  content: "";
  position: absolute;
  width: 39%;
  aspect-ratio: 1.79;
  top: 18%;
  left: 50px;
  background-image: url("../img/paper-airplane-image.png");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .contact__container::before {
    background-image: url("../img/paper-airplane-image@2x.png");
  }
}
@media (max-width: 1023px) {
  .contact__container::before {
    display: none;
  }
}

.contact__form {
  margin-left: auto;
  margin-right: 10px;
  width: 48%;
}
@media (max-width: 1023px) {
  .contact__form {
    margin-left: 0;
    width: 100%;
  }
}

.contact__form h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 15px;
}
@media (max-width: 1023px) {
  .contact__form h3 {
    margin-bottom: 8px;
  }
}
@media (max-width: 767px) {
  .contact__form h3 {
    font-size: 30px;
    line-height: 42px;
  }
}

.contact__form p {
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 35px;
  margin-left: 5px;
}
@media (max-width: 1023px) {
  .contact__form p {
    margin-left: 0;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .contact__form p {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 34px;
  }
}

.contact__form fieldset {
  border: none;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 5%;
  row-gap: 20px;
  margin-bottom: 32px;
}
@media (max-width: 1023px) {
  .contact__form fieldset {
    margin-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .contact__form fieldset {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 24px;
  }
}

.contact__form-field input,
.contact__form-field textarea {
  background-color: rgba(4, 20, 39, 0.1490196078);
  border: none;
  border-radius: 3px;
  color: #041827;
  padding: 15px;
  margin-left: 5px;
  width: 100%;
}
@media (max-width: 1023px) {
  .contact__form-field input,
.contact__form-field textarea {
    margin-left: 0;
  }
}
@media (hover: hover) {
  .contact__form-field input:hover:not(.focus-visible),
.contact__form-field textarea:hover:not(.focus-visible) {
    outline: 1px solid #f8fdff;
    outline-offset: 2px;
    cursor: pointer;
  }
  .contact__form-field input:hover:not(.focus-visible):disabled,
.contact__form-field textarea:hover:not(.focus-visible):disabled {
    outline: none;
  }
}
.contact__form-field input.focus-visible:focus,
.contact__form-field textarea.focus-visible:focus {
  outline: 1px solid #f8fdff;
  outline-offset: 2px;
  cursor: pointer;
}
.contact__form-field input.focus-visible:focus:disabled,
.contact__form-field textarea.focus-visible:focus:disabled {
  outline: none;
}
.contact__form-field input:disabled,
.contact__form-field textarea:disabled {
  pointer-events: none;
  border-color: #bfbfbf;
  background-color: #bfbfbf;
}
.contact__form-field input:disabled::placeholder,
.contact__form-field textarea:disabled::placeholder {
  color: #606060;
}

.contact__form-field--modified {
  grid-column: 1/-1;
}

.contact__form-field textarea {
  padding: 25px 15px 38px;
}
@media (max-width: 1023px) {
  .contact__form-field textarea {
    padding: 32px 15px 33px;
  }
}
@media (max-width: 767px) {
  .contact__form-field textarea {
    padding: 25px 15px 65px;
  }
}

.contact__form-field input::placeholder,
.contact__form-field textarea::placeholder {
  color: #ffffff;
}

.contact__form-container-inner {
  display: flex;
  align-items: flex-end;
  gap: 9%;
  flex-wrap: wrap;
  margin-left: 5px;
  position: relative;
}
@media (max-width: 1023px) {
  .contact__form-container-inner {
    margin-left: 0;
    gap: 12%;
  }
}
@media (max-width: 767px) {
  .contact__form-container-inner {
    flex-direction: column;
    gap: 25px;
  }
}

.contact__form button {
  font-size: 16px;
  line-height: 19px;
  background-color: #041427;
  color: #ffffff;
  border: none;
  padding: 13px 25px;
  width: 46%;
}
@media (max-width: 1023px) {
  .contact__form button {
    width: 40%;
    padding-top: 17px;
    padding-bottom: 9px;
  }
}
@media (max-width: 767px) {
  .contact__form button {
    width: 100%;
  }
}

.contact__form-label {
  position: relative;
  cursor: pointer;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  opacity: 0.8;
  padding-left: 24px;
  width: 41%;
}
.contact__form-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  transform: translateY(-50%);
}
@media (hover: hover) {
  .contact__form-label:hover:not(.focus-visible) {
    color: #041427;
  }
}
@media (max-width: 1023px) {
  .contact__form-label {
    padding-left: 26px;
  }
}
@media (max-width: 767px) {
  .contact__form-label {
    width: 100%;
    padding-left: 30px;
  }
  .contact__form-label::before {
    left: 5px;
  }
}

.contact__form-container-inner input {
  top: 50%;
  right: 44%;
}
@media (max-width: 1023px) {
  .contact__form-container-inner input {
    right: 47%;
  }
}
@media (max-width: 767px) {
  .contact__form-container-inner input {
    top: 95%;
    right: 98%;
  }
}

.contact__form-container-inner input:focus + .contact__form-label::before,
.contact__form-container-inner input:focus-visible + .contact__form-label::before {
  outline: 1px solid #f8fdff;
  outline-offset: 2px;
}

.contact__form-container-inner input:checked + .contact__form-label::after {
  content: "";
  position: absolute;
  width: 9px;
  height: 6px;
  background-image: url("../img/svg/check-mark.svg");
  background-size: 9px 6px;
  background-repeat: no-repeat;
  top: 50%;
  left: 0;
  transform: translate(35%, -50%);
}
@media (max-width: 767px) {
  .contact__form-container-inner input:checked + .contact__form-label::after {
    left: 5px;
  }
}

